"use client";
import React, { useState } from 'react';
import Link from 'next/link';
import {
    Home,
    DollarSign,
    Briefcase,
    TrendingUp,
    Users,
    Shield,
    Trophy,
    CheckCircle,
    ArrowRight,
    Zap,
    Layout,
    Target
} from 'lucide-react';
import DashboardHeader from '@/components/DashboardHeader';
import '@/styles/pages/Dashboard.css';

const QUICK_NAV = [
    { label: 'Trading', icon: <DollarSign size={20} />, path: '/dashboard/trading', color: 'var(--fintech-green)' },
    { label: 'Business', icon: <Briefcase size={20} />, path: '/dashboard/business', color: 'var(--accent)' },
    { label: 'Sales', icon: <TrendingUp size={20} />, path: '/dashboard/sales', color: '#ff3b30' },
    { label: 'Community', icon: <Users size={20} />, path: '/dashboard/community', color: 'var(--hero-purple)' }
];

const ONBOARDING_STEPS = [
    { id: '01', title: 'Initialization', desc: 'Secure Free Initial Access.', status: 'completed' },
    { id: '02', title: 'Formation', desc: 'LLC & EIN Setup Blueprints.', status: 'active' },
    { id: '03', title: 'Protection', desc: 'Secure $99/mo Business Insurance.', status: 'pending' },
    { id: '04', title: 'Growth', desc: 'Deploy Finance or Sales Division.', status: 'pending' }
];

const MOCK_LEADERBOARD = [
    { rank: 1, name: 'Julylan Johnson', sales: '$45,200', trading: '$128,400', total: '$173,600', roles: ['Sales', 'Trading'], trend: '+12%' },
    { rank: 2, name: 'Donta Lewis', sales: '$38,900', trading: '$0', total: '$38,900', roles: ['Sales'], trend: '+8%' },
    { rank: 3, name: 'Sarah Vision', sales: '$12,400', trading: '$52,100', total: '$64,500', roles: ['Trading'], trend: '+15%' },
    { rank: 4, name: 'Marcus Growth', sales: '$22,800', trading: '$18,500', total: '$41,300', roles: ['Sales', 'Trading'], trend: '+22%' },
    { rank: 5, name: 'Elena Yield', sales: '$0', trading: '$92,000', total: '$92,000', roles: ['Trading'], trend: '+5%' }
];

export default function DashboardPage() {
    const [activeTab, setActiveTab] = useState('ALL');

    const filteredLeaderboard = MOCK_LEADERBOARD.filter(entry => {
        if (activeTab === 'ALL') return true;
        if (activeTab === 'SALES') return entry.roles.includes('Sales');
        if (activeTab === 'TRADING') return entry.roles.includes('Trading');
        if (activeTab === 'DUAL') return entry.roles.includes('Sales') && entry.roles.includes('Trading');
        return true;
    }).sort((a, b) => {
        const getVal = (s: string) => parseFloat(s.replace(/[^0-9.]/g, ''));
        return getVal(b.total) - getVal(a.total);
    }).map((entry, idx) => ({ ...entry, rank: idx + 1 }));

    return (
        <div className="dash-overview animate-fade-in">
            <DashboardHeader
                title="Central Command"
                subtitle="OS Status: Optimal â€¢ Vision Level: Strategic"
            />

            {/* Quick Navigation Buttons */}
            <div className="quick-nav-grid">
                {QUICK_NAV.map(nav => (
                    <Link key={nav.path} href={nav.path} className="nav-btn">
                        <div className="icon-box" style={{ color: nav.color, backgroundColor: `${nav.color}15` }}>
                            {nav.icon}
                        </div>
                        <span>{nav.label}</span>
                    </Link>
                ))}
                <Link href="/dashboard/profile" className="nav-btn">
                    <div className="icon-box" style={{ color: 'var(--secondary)', backgroundColor: 'rgba(255,255,255,0.05)' }}>
                        <Layout size={20} />
                    </div>
                    <span>Profile</span>
                </Link>
            </div>

            {/* Onboarding Process Tracker */}
            <section className="onboarding-tracker">
                <div className="tracker-header">
                    <div className="tracker-title">
                        <h3>Onboarding Protocol</h3>
                        <p className="text-secondary text-sm">Deployment Progress: Phase 02 Initializing</p>
                    </div>
                    <div className="progress-pct font-mono text-accent">25%</div>
                </div>

                <div className="onboarding-steps">
                    {ONBOARDING_STEPS.map(step => (
                        <div key={step.id} className={`ob-step ${step.status}`}>
                            <span className="step-indicator">STEP {step.id}</span>
                            <h4>
                                {step.status === 'completed' && <CheckCircle size={14} className="text-green-500 inline mr-1" />}
                                {step.title}
                            </h4>
                            <p>{step.desc}</p>
                            {step.status === 'active' && <div className="active-pulse"></div>}
                        </div>
                    ))}
                </div>
            </section>

            {/* Analytical Leaderboard */}
            <section className="advanced-leaderboard glass-panel p-6">
                <div className="flex-between mb-2">
                    <div className="flex items-center gap-2">
                        <Trophy size={20} className="text-accent" />
                        <h3 className="text-lg font-bold">Visionary Leaderboard</h3>
                    </div>
                    <div className="leaderboard-tabs">
                        {['ALL', 'SALES', 'TRADING', 'DUAL'].map(tab => (
                            <button
                                key={tab}
                                onClick={() => setActiveTab(tab)}
                                className={`tab-btn ${activeTab === tab ? 'active' : ''}`}
                            >
                                {tab}
                            </button>
                        ))}
                    </div>
                </div>
                <p className="text-secondary text-sm mb-6">Real-time performance metrics across all active divisions.</p>

                <div className="analytical-table-wrapper">
                    <table className="analytical-table">
                        <thead>
                            <tr>
                                <th>Rank</th>
                                <th>Visionary</th>
                                <th>Sales Revenue</th>
                                <th>Trading Yield</th>
                                <th>Total Impact</th>
                                <th>Trend</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredLeaderboard.map(user => (
                                <tr key={user.name}>
                                    <td>
                                        <div className={`rank-badge rank-top-${user.rank}`}>
                                            {user.rank}
                                        </div>
                                    </td>
                                    <td>
                                        <div className="user-cell">
                                            <span className="font-bold">{user.name}</span>
                                            <div className="flex gap-2">
                                                {user.roles.includes('Sales') && <span className="division-tag sales">SALES</span>}
                                                {user.roles.includes('Trading') && <span className="division-tag trading">TRADING</span>}
                                                {user.roles.length > 1 && <span className="division-tag dual">DOUBLE</span>}
                                            </div>
                                        </div>
                                    </td>
                                    <td><span className="amount-text">{user.sales}</span></td>
                                    <td><span className="amount-text">{user.trading}</span></td>
                                    <td><span className="amount-text text-accent font-bold">{user.total}</span></td>
                                    <td><span className="pos font-bold">{user.trend}</span></td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </section>

            {/* System Insights */}
            <div className="dash-grid mt-6">
                <div className="glass-panel p-6">
                    <div className="flex items-center gap-2 mb-4">
                        <Zap size={20} className="text-yellow-500" />
                        <h4 className="font-bold uppercase tracking-widest text-xs">Live Ecosystem Logs</h4>
                    </div>
                    <div className="space-y-3 font-mono text-[10px] text-secondary">
                        <div className="flex justify-between">
                            <span>Julylan J. generated $420 commission</span>
                            <span className="text-accent">2s ago</span>
                        </div>
                        <div className="flex justify-between">
                            <span>Risk protocol scanned Sales Division</span>
                            <span className="text-accent">1m ago</span>
                        </div>
                        <div className="flex justify-between">
                            <span>New visionary entered Phase 01</span>
                            <span className="text-accent">3m ago</span>
                        </div>
                    </div>
                </div>

                <div className="glass-panel p-6 flex flex-col justify-between">
                    <div>
                        <div className="flex items-center gap-2 mb-4">
                            <Target size={20} className="text-accent" />
                            <h4 className="font-bold uppercase tracking-widest text-xs">Next Critical Action</h4>
                        </div>
                        <p className="text-sm">Complete your LLC Formation to unlock the Protection Protocol and begin scaling.</p>
                    </div>
                    <Link href="/dashboard/business" className="btn-primary mt-4 text-center text-xs flex items-center justify-center gap-2">
                        Execute Formation <ArrowRight size={14} />
                    </Link>
                </div>
            </div>
        </div>
    );
}
